@mixin col($name) {
	&[data-column*="#{$name}"] {
		@content; } }

.table {
	display: table;
	table-layout: fixed;
	width: 100%;
	background: $white;
	box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
	&__thead {
		display: table-header-group;
		color: $white;
		background: $primary;
		font-size: 18px;
		font-weight: 700;
		@include sm {
			font-size: 16px; } }
	&__tbody {
		display: table-row-group;
		font-size: 16px;
		@include sm {
			font-size: 13px; } }
	&__tr {
		display: table-row;
		&:not(:last-child) {
			.table__td {
				border-bottom: 1px solid #e0e0e0; } } }
	&__th,
	&__td {
		display: table-cell;
		vertical-align: middle;
		padding: 15px 10px;
		line-height: 1.3;
		text-align: center;
		@include sm {
			padding-left: 8px;
			padding-right: 8px; }
		&:first-child {
			padding-left: 20px;
			@include sm {
				padding-left: 10px; } }
		&:last-child {
			padding-right: 20px;
			@include sm {
				padding-right: 10px; } }
		@include col('checkbox') {
			width: 61px; } }
	&__th {
		padding-top: 20px;
		padding-bottom: 20px;
		@include sm {
			padding-top: 15px;
			padding-bottom: 15px; } }
	&__btn {
		padding-top: 10px;
		padding-bottom: 11px;
		max-width: 110px;
		height: auto;
		line-height: 1.2;
		white-space: normal;
		@include sm {
			max-width: 100%;
			width: 100px;
			font-size: 14px; } } }
